import Vue from "vue";
import { request } from "../core/api";
import { spaceship } from "../operators";

export const emojiBaseUrl = "https://cdn.mili.us/assets/joypixels/v5";
export const skinTones = [
	null,
	"1f3fb",
	"1f3fc",
	"1f3fd",
	"1f3fe",
	"1f3ff"
];

let categoriesJson = null;
let emojisJson = null;
let isLoaded = false;
let resolvers = [];

Vue.directive("emojify", (el, binding, vnode) =>
{
	vnode.key = vnode.elm.innerHTML;

	ensureEmojisReady()
		.then(() => el.innerHTML = replaceEmoji(vnode.elm.innerHTML));
});

export function initializeEmoji()
{
	loadCategories();
}

export function ensureEmojisReady()
{
	return new Promise(resolve =>
	{
		if (isLoaded)
			resolve();

		resolvers.push(resolve);
	});
}

export function getCategories()
{
	return categoriesJson;
}

export function getCategory(category)
{
	if (categoriesJson === null)
		return null;

	return categoriesJson.filter(c => c.category === category);
}

export function getEmoji(codePoint)
{
	return emojisJson.find(emoji => emoji.code_points.base === codePoint);
}

export function getEmojisForCategory(category)
{
	return emojisJson
		.filter(emoji => emoji.category === category)
		.sort((a, b) => spaceship(a.order, b.order));
}

export function replaceEmoji(str)
{
	for (let i in emojisJson)
	{
		let emoji = emojisJson[i];

		str = str.split(emoji.unicode).join(`<img src="${emojiBaseUrl}/svg/${emoji.code_points.base}.svg" class="emoji" alt="${emoji.name}" title="${emoji.name}"/>`);
	}

	return str;
}

function convertCodePoint(codePoint, separator)
{
	if (codePoint.indexOf("-") > -1)
	{
		let parts = [];
		let s = codePoint.split("-");

		for (let i = 0; i < s.length; i++)
		{
			let part = parseInt(s[i], 16);

			if (part >= 0x10000 && part <= 0x10FFFF)
			{
				let hi = Math.floor((part - 0x10000) / 0x400) + 0xD800;
				let lo = ((part - 0x10000) % 0x400) + 0xDC00;
				part = (String.fromCharCode(hi) + String.fromCharCode(lo));
			}
			else
			{
				part = String.fromCharCode(part);
			}

			parts.push(part);
		}

		return parts.join(separator);
	}
	else
	{
		let s = parseInt(codePoint, 16);

		if (s >= 0x10000 && s <= 0x10FFFF)
		{
			let hi = Math.floor((s - 0x10000) / 0x400) + 0xD800;
			let lo = ((s - 0x10000) % 0x400) + 0xDC00;

			return (String.fromCharCode(hi) + String.fromCharCode(lo));
		}
		else
		{
			return String.fromCharCode(s);
		}
	}
}

function loadCategories()
{
	request(`${emojiBaseUrl}/categories.json`)
		.then(r => r.json())
		.then(r => onCategoriesLoaded(r))
		.catch(err => console.error(err));
}

function loadEmojis()
{
	request(`${emojiBaseUrl}/emoji.json`)
		.then(r => r.json())
		.then(r => onEmojisLoaded(r))
		.catch(err => console.error(err));
}

function transpileJsonData()
{
	for (let i in categoriesJson)
	{
		categoriesJson[i].imageUrl = `${emojiBaseUrl}/category-icons/${categoriesJson[i].category}.svg`;
	}

	for (let i in emojisJson)
	{
		emojisJson[i].unicode = convertCodePoint(emojisJson[i].code_points.output, "");
		emojisJson[i].unicodeParts = convertCodePoint(emojisJson[i].code_points.output, ",").split(",");
		emojisJson[i].url = `${emojiBaseUrl}/svg/${emojisJson[i].code_points.base}.svg`;
		emojisJson[i].urlSprite = `${emojiBaseUrl}/sprite/joypixels-sprite.svg#jp-${emojisJson[i].code_points.base}`;
	}

	emojisJson.sort((a, b) => spaceship(b.code_points.output.length, a.code_points.output.length));

	isLoaded = true;

	for (let resolve of resolvers)
		resolve();

	resolvers = undefined;
}

function onCategoriesLoaded(categories)
{
	categoriesJson = categories;
	loadEmojis();
}

function onEmojisLoaded(emojis)
{
	emojisJson = Object.values(emojis);
	transpileJsonData();
}
